/* src/pages/AdminForum.css */

.admin-forum-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 100px 20px;
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  }
  
  /* ========== HEADER ========== */
  .admin-forum-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
    padding-bottom: 30px;
    border-bottom: 2px solid #e0e0e0;
  }
  
  .header-content h1 {
    color: #3B6B8F;
    font-size: 32px;
    font-weight: 700;
    margin: 0 0 8px 0;
  }
  
  .header-content p {
    color: #666;
    font-size: 16px;
    margin: 0;
  }
  
  .year-selector {
    display: flex;
    align-items: center;
    gap: 12px;
  }
  
  .year-selector label {
    font-weight: 600;
    color: #333;
    font-size: 14px;
  }
  
  .year-selector select {
    padding: 10px 16px;
    border: 2px solid #3B6B8F;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 600;
    color: #3B6B8F;
    background: white;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .year-selector select:hover {
    background: #f0f7ff;
    transform: translateY(-2px);
  }
  
  .year-selector select:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(59, 107, 143, 0.1);
  }
  
  /* ========== MESSAGE BANNER ========== */
  .message-banner {
    padding: 16px 20px;
    border-radius: 8px;
    margin-bottom: 24px;
    font-weight: 500;
    animation: slideDown 0.3s ease;
  }
  
  @keyframes slideDown {
    from {
      opacity: 0;
      transform: translateY(-10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .message-banner.success {
    background: #d4edda;
    color: #155724;
    border: 1px solid #c3e6cb;
  }
  
  .message-banner.error {
    background: #f8d7da;
    color: #721c24;
    border: 1px solid #f5c6cb;
  }
  
  /* ========== TABS ========== */
  .admin-forum-tabs {
    display: flex;
    gap: 8px;
    margin-bottom: 32px;
    border-bottom: 2px solid #e0e0e0;
    flex-wrap: wrap;
  }
  
  .admin-forum-tabs button {
    padding: 12px 24px;
    background: transparent;
    border: none;
    border-bottom: 3px solid transparent;
    color: #666;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    margin-bottom: -2px;
  }
  
  .admin-forum-tabs button:hover {
    color: #3B6B8F;
    background: rgba(59, 107, 143, 0.05);
  }
  
  .admin-forum-tabs button.active {
    color: #3B6B8F;
    border-bottom-color: #3B6B8F;
  }
  
  /* ========== CONTENT ========== */
  .admin-forum-content {
    background: white;
    border-radius: 12px;
    padding: 32px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
    margin-bottom: 24px;
  }
  
  .tab-content {
    animation: fadeIn 0.3s ease;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .form-section {
    margin-bottom: 40px;
  }
  
  .form-section:last-child {
    margin-bottom: 0;
  }
  
  .form-section h2 {
    color: #3B6B8F;
    font-size: 22px;
    font-weight: 700;
    margin: 0 0 24px 0;
    padding-bottom: 12px;
    border-bottom: 2px solid #e0e0e0;
  }
  
  /* ========== FORM ELEMENTS ========== */
  .form-group {
    margin-bottom: 24px;
  }
  
  .form-group label {
    display: block;
    font-weight: 600;
    color: #333;
    margin-bottom: 8px;
    font-size: 14px;
  }
  
  .form-group input[type="text"],
  .form-group input[type="url"],
  .form-group textarea,
  .form-group select {
    width: 100%;
    padding: 12px 16px;
    border: 2px solid #e0e0e0;
    border-radius: 8px;
    font-size: 15px;
    font-family: inherit;
    transition: all 0.3s ease;
    background: white;
    box-sizing: border-box;
  }
  
  .form-group input:focus,
  .form-group textarea:focus,
  .form-group select:focus {
    outline: none;
    border-color: #3B6B8F;
    box-shadow: 0 0 0 3px rgba(59, 107, 143, 0.1);
  }
  
  .form-group textarea {
    resize: vertical;
    min-height: 100px;
    line-height: 1.6;
  }
  
  .form-group small {
    display: block;
    margin-top: 6px;
    color: #666;
    font-size: 13px;
    line-height: 1.4;
  }
  
  .form-row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  
  /* ========== CHECKBOX GROUP ========== */
  .checkbox-group {
    padding: 16px;
    background: #f8f9fa;
    border-radius: 8px;
    border: 2px solid #e0e0e0;
  }
  
  .checkbox-group label {
    display: flex;
    align-items: center;
    gap: 12px;
    cursor: pointer;
    font-weight: 500;
  }
  
  .checkbox-group input[type="checkbox"] {
    width: 20px;
    height: 20px;
    cursor: pointer;
    accent-color: #3B6B8F;
  }
  
  .checkbox-group span {
    color: #333;
    font-size: 15px;
  }
  
  /* ========== IMAGE UPLOAD ========== */
  .image-upload-group {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 24px;
  }
  
  .image-upload-item label {
    display: block;
    font-weight: 600;
    color: #333;
    margin-bottom: 12px;
    font-size: 14px;
  }
  
  .image-upload-item input[type="file"] {
    width: 100%;
    padding: 12px;
    border: 2px dashed #3B6B8F;
    border-radius: 8px;
    background: #f0f7ff;
    cursor: pointer;
    transition: all 0.3s ease;
    box-sizing: border-box;
  }
  
  .image-upload-item input[type="file"]:hover {
    background: #e6f2ff;
    border-color: #2a5570;
  }
  
  .uploading-text {
    margin-top: 8px;
    color: #3B6B8F;
    font-size: 14px;
    font-weight: 600;
  }
  
  .image-preview {
    margin-top: 16px;
    border-radius: 8px;
    overflow: hidden;
    border: 2px solid #e0e0e0;
  }
  
  .image-preview img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    display: block;
  }
  
  /* ========== ADD ITEM FORMS ========== */
  .add-item-form {
    display: grid;
    gap: 12px;
    padding: 20px;
    background: #f8f9fa;
    border-radius: 8px;
    margin-bottom: 24px;
  }
  
  .add-item-form input,
  .add-item-form textarea,
  .add-item-form select {
    padding: 12px 16px;
    border: 2px solid #e0e0e0;
    border-radius: 8px;
    font-size: 14px;
    font-family: inherit;
    transition: all 0.3s ease;
    box-sizing: border-box;
  }
  
  .add-item-form input:focus,
  .add-item-form textarea:focus,
  .add-item-form select:focus {
    outline: none;
    border-color: #3B6B8F;
    box-shadow: 0 0 0 3px rgba(59, 107, 143, 0.1);
  }
  
  .add-item-form.agenda-form {
    grid-template-columns: 1fr 2fr;
  }
  
  .add-item-form.agenda-form textarea {
    grid-column: 1 / -1;
  }
  
  .add-item-form.speaker-form {
    grid-template-columns: 1fr 1fr;
  }
  
  .add-item-form.speaker-form textarea {
    grid-column: 1 / -1;
  }
  
  /* ========== SPONSOR FORM ========== */
  .add-item-form.sponsor-form {
    grid-template-columns: 1fr 1fr;
  }
  
  .sponsor-logo-upload {
    grid-column: 1 / -1;
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  
  .sponsor-logo-upload label {
    font-weight: 600;
    color: #333;
    font-size: 14px;
  }
  
  .sponsor-logo-upload input[type="file"] {
    padding: 10px;
    border: 2px dashed #3B6B8F;
    background: white;
  }
  
  .sponsor-logo-preview {
    max-width: 150px;
    max-height: 80px;
    object-fit: contain;
    margin-top: 8px;
    padding: 8px;
    background: white;
    border: 1px solid #e0e0e0;
    border-radius: 4px;
  }
  
  .btn-add {
    grid-column: 1 / -1;
    padding: 12px 24px;
    background: #3B6B8F;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .btn-add:hover {
    background: #2a5570;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(59, 107, 143, 0.3);
  }
  
  .btn-add:active {
    transform: translateY(0);
  }
  
  /* ========== LINKS LIST ========== */
  .links-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }
  
  .link-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
    background: white;
    border: 2px solid #e0e0e0;
    border-radius: 8px;
    transition: all 0.3s ease;
  }
  
  .link-item:hover {
    border-color: #3B6B8F;
    box-shadow: 0 2px 8px rgba(59, 107, 143, 0.1);
  }
  
  .link-info {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 4px;
  }
  
  .link-info strong {
    color: #333;
    font-size: 15px;
  }
  
  .link-info a {
    color: #3B6B8F;
    font-size: 13px;
    text-decoration: none;
    word-break: break-all;
  }
  
  .link-info a:hover {
    text-decoration: underline;
  }
  
  .btn-remove {
    padding: 8px 16px;
    background: #dc3545;
    color: white;
    border: none;
    border-radius: 6px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    white-space: nowrap;
  }
  
  .btn-remove:hover {
    background: #c82333;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(220, 53, 69, 0.3);
  }
  
  .btn-remove:active {
    transform: translateY(0);
  }

  /* ========== EDIT BUTTON ========== */
  .btn-edit {
    padding: 8px 16px;
    background: #3B6B8F;
    color: white;
    border: none;
    border-radius: 6px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    white-space: nowrap;
  }

  .btn-edit:hover {
    background: #2a5570;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(59, 107, 143, 0.3);
  }

  .btn-edit:active {
    transform: translateY(0);
  }

  /* ========== MOVE BUTTONS ========== */
  .move-buttons {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }

  .btn-move {
    padding: 4px 10px;
    background: #6c757d;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.2s ease;
    line-height: 1;
  }

  .btn-move:hover:not(:disabled) {
    background: #5a6268;
    transform: scale(1.1);
  }

  .btn-move:disabled {
    background: #dee2e6;
    color: #adb5bd;
    cursor: not-allowed;
  }

  /* ========== ITEM ACTIONS ========== */
  .item-actions {
    display: flex;
    align-items: center;
    gap: 8px;
    flex-shrink: 0;
  }

  /* ========== EDIT BUTTONS IN FORM ========== */
  .edit-buttons {
    grid-column: 1 / -1;
    display: flex;
    gap: 12px;
  }

  .btn-save-edit {
    flex: 1;
    padding: 12px 24px;
    background: #28a745;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
  }

  .btn-save-edit:hover {
    background: #218838;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(40, 167, 69, 0.3);
  }

  .btn-cancel-edit {
    padding: 12px 24px;
    background: #6c757d;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
  }

  .btn-cancel-edit:hover {
    background: #5a6268;
    transform: translateY(-2px);
  }

  /* ========== EDITING STATE HIGHLIGHT ========== */
  .link-item.editing,
  .agenda-item.editing,
  .sponsor-item.editing {
    border-color: #28a745;
    background: #f0fff4;
    box-shadow: 0 0 0 3px rgba(40, 167, 69, 0.2);
  }

  /* ========== DRAG HANDLE ========== */
  .drag-handle {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 4px;
    cursor: grab;
    color: #adb5bd;
    font-size: 18px;
    user-select: none;
    touch-action: none;
    transition: all 0.2s ease;
    border-radius: 4px;
    margin-right: 8px;
  }

  .drag-handle:hover {
    color: #3B6B8F;
    background: rgba(59, 107, 143, 0.1);
  }

  .drag-handle:active {
    cursor: grabbing;
  }

  .drag-icon {
    letter-spacing: 2px;
    font-weight: bold;
  }

  /* ========== DRAGGING STATE ========== */
  .link-item.dragging,
  .agenda-item.dragging,
  .sponsor-item.dragging {
    background: #e3f2fd;
    border-color: #2196F3;
    box-shadow: 0 8px 25px rgba(33, 150, 243, 0.3);
    z-index: 1000;
  }

  /* Update item layouts to include drag handle */
  .link-item {
    display: flex;
    align-items: center;
  }

  .sponsor-item {
    display: flex;
    align-items: center;
  }
  
  /* ========== SPONSORS LIST ========== */
  .sponsors-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
  
  .sponsor-item {
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 16px;
    background: white;
    border: 2px solid #e0e0e0;
    border-radius: 8px;
    transition: all 0.3s ease;
  }
  
  .sponsor-item:hover {
    border-color: #3B6B8F;
    box-shadow: 0 2px 8px rgba(59, 107, 143, 0.1);
  }
  
  .sponsor-logo-thumb {
    width: 80px;
    height: 60px;
    object-fit: contain;
    padding: 8px;
    background: #f8f9fa;
    border-radius: 6px;
    border: 1px solid #e0e0e0;
  }
  
  .sponsor-info {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 6px;
  }
  
  .sponsor-info strong {
    color: #333;
    font-size: 16px;
  }
  
  .sponsor-info a {
    color: #3B6B8F;
    font-size: 14px;
    text-decoration: none;
  }
  
  .sponsor-info a:hover {
    text-decoration: underline;
  }
  
  .tier-badge {
    display: inline-block;
    padding: 4px 12px;
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
    border-radius: 4px;
    letter-spacing: 0.5px;
  }
  
  .tier-badge.platinum {
    background: linear-gradient(135deg, #E5E4E2 0%, #BCC6CC 100%);
    color: #333;
  }
  
  .tier-badge.gold {
    background: linear-gradient(135deg, #FFD700 0%, #FFA500 100%);
    color: #333;
  }
  
  .tier-badge.silver {
    background: linear-gradient(135deg, #C0C0C0 0%, #808080 100%);
    color: white;
  }
  
  .tier-badge.bronze {
    background: linear-gradient(135deg, #CD7F32 0%, #8B4513 100%);
    color: white;
  }
  
  .tier-badge.standard {
    background: #3B6B8F;
    color: white;
  }
  
  /* ========== AGENDA LIST ========== */
  .agenda-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  .agenda-item {
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 20px;
    background: white;
    border: 2px solid #e0e0e0;
    border-left: 4px solid #3B6B8F;
    border-radius: 8px;
    transition: all 0.3s ease;
  }

  .agenda-item:hover {
    border-color: #3B6B8F;
    box-shadow: 0 4px 12px rgba(59, 107, 143, 0.1);
  }

  .agenda-time {
    font-weight: 700;
    color: #3B6B8F;
    font-size: 16px;
    white-space: nowrap;
    min-width: 100px;
  }

  .agenda-details {
    display: flex;
    flex-direction: column;
    gap: 8px;
    flex: 1;
  }

  .agenda-details strong {
    color: #333;
    font-size: 16px;
  }

  .agenda-speaker {
    color: #666;
    font-size: 14px;
    font-style: italic;
    margin: 0;
  }

  .agenda-description {
    color: #555;
    font-size: 14px;
    line-height: 1.5;
    margin: 0;
  }
  
  /* ========== ACTIONS ========== */
  .admin-forum-actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    padding: 24px 0;
    flex-wrap: wrap;
  }
  
  .actions-left {
    display: flex;
    gap: 12px;
    flex-wrap: wrap;
  }
  
  .btn-clear {
    padding: 14px 28px;
    background: #FF9800;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .btn-clear:hover {
    background: #F57C00;
    transform: translateY(-3px);
    box-shadow: 0 8px 20px rgba(255, 152, 0, 0.4);
  }
  
  .btn-delete {
    padding: 14px 28px;
    background: #dc3545;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .btn-delete:hover {
    background: #c82333;
    transform: translateY(-3px);
    box-shadow: 0 8px 20px rgba(220, 53, 69, 0.4);
  }
  
  .btn-save {
    padding: 14px 32px;
    background: linear-gradient(135deg, #3B6B8F 0%, #2a5570 100%);
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 12px rgba(59, 107, 143, 0.3);
  }
  
  .btn-save:hover:not(:disabled) {
    transform: translateY(-3px);
    box-shadow: 0 8px 20px rgba(59, 107, 143, 0.4);
  }
  
  .btn-save:active:not(:disabled) {
    transform: translateY(-1px);
  }
  
  .btn-save:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
  
  /* ========== DELETE/CLEAR CONFIRMATION MODALS ========== */
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    animation: fadeIn 0.3s ease;
  }
  
  .modal-content {
    background: white;
    padding: 32px;
    border-radius: 12px;
    max-width: 500px;
    width: 90%;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
    animation: slideUp 0.3s ease;
  }
  
  @keyframes slideUp {
    from {
      opacity: 0;
      transform: translateY(30px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .modal-content h3 {
    font-size: 24px;
    margin-bottom: 16px;
  }
  
  .modal-content p {
    color: #555;
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 12px;
  }
  
  .clear-list {
    margin: 16px 0;
    padding-left: 24px;
    color: #555;
  }
  
  .clear-list li {
    margin-bottom: 8px;
    font-size: 15px;
  }
  
  .warning-text {
    color: #dc3545;
    font-weight: 600;
    font-size: 14px;
    margin-top: 16px;
  }
  
  .modal-actions {
    display: flex;
    gap: 12px;
    justify-content: flex-end;
    margin-top: 24px;
  }
  
  .btn-cancel {
    padding: 12px 24px;
    background: #6c757d;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .btn-cancel:hover {
    background: #5a6268;
    transform: translateY(-2px);
  }
  
  .btn-confirm-clear {
    padding: 12px 24px;
    background: #FF9800;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .btn-confirm-clear:hover {
    background: #F57C00;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(255, 152, 0, 0.4);
  }
  
  .btn-confirm-delete {
    padding: 12px 24px;
    background: #dc3545;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .btn-confirm-delete:hover {
    background: #c82333;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(220, 53, 69, 0.4);
  }
  
  /* ========== LOADING STATE ========== */
  .admin-forum-loading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 400px;
    gap: 20px;
  }
  
  .loader {
    width: 50px;
    height: 50px;
    border: 5px solid #f3f3f3;
    border-top: 5px solid #3B6B8F;
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  
  .admin-forum-loading p {
    color: #666;
    font-size: 16px;
    font-weight: 500;
  }
  
  .quill {
    background: white;
    border-radius: 8px;
    margin-bottom: 8px;
  }
  
  .ql-container {
    min-height: 200px;
    font-size: 15px;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
  }
  
  .ql-toolbar {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    background: #f8f9fa;
  }
  
  .ql-editor {
    min-height: 200px;
  }
  
  .ql-editor::before {
    font-style: normal;
    color: #999;
  }
  
  .ql-editor p {
    margin-bottom: 12px;
  }
  /* ========== RESPONSIVE DESIGN ========== */
  @media (max-width: 1024px) {
    .admin-forum-header {
      flex-direction: column;
      align-items: flex-start;
      gap: 20px;
    }
  
    .image-upload-group {
      grid-template-columns: 1fr;
    }
  
    .add-item-form.speaker-form,
    .add-item-form.sponsor-form {
      grid-template-columns: 1fr;
    }
  }
  
  @media (max-width: 768px) {
    .admin-forum-container {
      padding: 20px 16px;
    }

    .header-content h1 {
      font-size: 24px;
    }

    .admin-forum-content {
      padding: 20px;
    }

    .form-row {
      grid-template-columns: 1fr;
    }

    .add-item-form.agenda-form {
      grid-template-columns: 1fr;
    }

    .agenda-item {
      grid-template-columns: 1fr;
      gap: 12px;
    }

    .agenda-time {
      padding-bottom: 8px;
      border-bottom: 1px solid #e0e0e0;
    }

    .link-item,
    .sponsor-item {
      flex-direction: column;
      gap: 12px;
      align-items: flex-start;
    }

    .item-actions {
      width: 100%;
      flex-wrap: wrap;
      justify-content: flex-start;
    }

    .move-buttons {
      flex-direction: row;
    }

    .btn-edit,
    .btn-remove {
      flex: 1;
    }

    .edit-buttons {
      flex-direction: column;
    }

    .btn-save-edit,
    .btn-cancel-edit {
      width: 100%;
    }

    .admin-forum-tabs {
      overflow-x: auto;
    }

    .admin-forum-tabs button {
      white-space: nowrap;
      font-size: 14px;
      padding: 10px 16px;
    }

    /* Drag handle responsive */
    .drag-handle {
      padding: 12px 8px;
    }

    .agenda-item {
      flex-wrap: wrap;
    }

    .agenda-item .drag-handle {
      order: 1;
    }

    .agenda-item .agenda-time {
      order: 2;
      min-width: auto;
    }

    .agenda-item .agenda-details {
      order: 3;
      width: 100%;
      margin-top: 8px;
    }

    .agenda-item .item-actions {
      order: 4;
      width: 100%;
      margin-top: 12px;
    }
  }
  
  @media (max-width: 480px) {
    .header-content h1 {
      font-size: 20px;
    }
  
    .year-selector {
      width: 100%;
    }
  
    .year-selector select {
      flex: 1;
    }
  
    .form-section h2 {
      font-size: 18px;
    }
  
    .sponsor-logo-thumb {
      width: 60px;
      height: 45px;
    }
  }
  
  /* ========== FOCUS STYLES FOR ACCESSIBILITY ========== */
  button:focus-visible,
  input:focus-visible,
  select:focus-visible,
  textarea:focus-visible {
    outline: 3px solid #3B6B8F;
    outline-offset: 2px;
  }
  
  /* ========== CUSTOM SCROLLBAR ========== */
  .admin-forum-content::-webkit-scrollbar {
    width: 8px;
  }
  
  .admin-forum-content::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 4px;
  }
  
  .admin-forum-content::-webkit-scrollbar-thumb {
    background: #3B6B8F;
    border-radius: 4px;
  }
  
  .admin-forum-content::-webkit-scrollbar-thumb:hover {
    background: #2a5570;
  }